html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
input[type="reset"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  text-decoration: none;
  color: inherit;
}

input, textarea, button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
  color: inherit;
  line-height: inherit;
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
}

input::placeholder, textarea::placeholder {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
}

textarea {
  resize: none;
  border: 0;
}

button, input[type="submit"], input[type="reset"] {
  background-color: transparent;
  overflow: visible;
}

img {
  max-width: 100%;
}

main {
  display: block;
}

:root {
  --grid-gap:          30px;
  --container-padding: 15px;
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 1560px;
  padding-left: 15px;
  padding-right: 15px;
  padding-left: var(--container-padding, 15px);
  padding-right: var(--container-padding, 15px);
}

.grid {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin-bottom: -30px;
  margin-left: calc(var(--grid-gap, 30px) / -2);
  margin-right: calc(var(--grid-gap, 30px) / -2);
  margin-bottom: calc(0px - var(--grid-gap, 30px));
}

.grid--no-bottom-gap {
  margin-bottom: 0;
}

.grid--no-bottom-gap-all {
  margin-bottom: 0;
}

.grid--no-bottom-gap-all .col {
  margin-bottom: 0;
}

.col {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 30px;
  padding-left: calc(var(--grid-gap, 30px) / 2);
  padding-right: calc(var(--grid-gap, 30px) / 2);
  margin-bottom: var(--grid-gap, 30px);
}

