.car-list > li:before {
  content: counter(listcar);
  counter-increment: listcar;
}

.car-list__item {
  position: relative;
  padding-right: 2.5rem;
  padding-bottom: 1.5625rem;
  padding-top: 1.5625rem;
}

.car-list__item::after {
  content: '';
  position: absolute;
  width: 100%;
  z-index: 0;
  background-color: #ffb32a;
  height: 1px;
  width: 100%;
  left: 0;
  bottom: 0.625rem;
}

.car-list__item::before {
  display: inline-block;
  text-align: center;
  font-size: 0.875em;
  line-height: 1.25rem;
  font-weight: 700;
  position: absolute;
  z-index: 1;
  bottom: 0;
  right: 0;
  background-color: #ffb32a;
  color: #fff;
  border-radius: 1.25rem;
  height: 1.25rem;
  min-width: 1.25rem;
}

.article p:not(:last-child), .article ul:not(:last-child), .article ol:not(:last-child) {
  margin-bottom: 1.5625rem;
}

.article ul li::before {
  content: '\2022';
  display: inline-block;
}

.article ul li::before {
  display: inline-block;
  color: #ffb32a;
  margin-left: .3em;
}

.article ol {
  counter-reset: listtext;
}

.article ol > li:before {
  content: counter(listtext) ")";
  counter-increment: listtext;
}

.article ol li::before {
  display: inline-block;
  margin-left: .3em;
}

.article a {
  color: #1678e5;
  text-decoration: underline;
}

.item-descr__title {
  color: #1678e5;
  text-decoration: underline;
  font-size: 1.3125em;
  margin-bottom: 0.9375rem;
}

.item-descr__price-block {
  margin-bottom: 0.75rem;
}

.item-descr__meta {
  max-width: 540px;
  margin-bottom: 0.625rem;
}

.item-descr__text {
  max-width: 565px;
  position: relative;
  padding-bottom: 0.75rem;
  margin-bottom: 1.5625rem;
}

.item-descr__text::before {
  content: '';
  background-color: #f3f3f3;
  height: 1px;
  width: 100%;
  max-width: 540px;
  bottom: 0;
  right: 0;
  position: absolute;
}

.item-descr__btn {
  margin-bottom: 1.5625rem;
}

.item-meta {
  display: flex;
  justify-content: flex-start;
  align-items: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  border-top: 1px solid #f3f3f3;
  border-bottom: 1px solid #f3f3f3;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
}

.item-meta__item:not(:first-child) {
  padding-right: 0.9375rem;
  border-right: 1px solid #f3f3f3;
}

.item-meta__item:not(:last-child) {
  padding-left: 0.9375rem;
}

.item-del-meta {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}

.item-del-meta__item {
  color: #636363;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}

.item-del-meta__item:not(:first-child) {
  padding-right: 0.9375rem;
  border-right: 1px solid currentColor;
}

.item-del-meta__item:not(:last-child) {
  padding-left: 0.9375rem;
}

.item-del-meta__icon {
  font-size: 0;
  margin-left: 0.625rem;
}

.item-del-meta__icon .icon {
  font-size: 1.875rem;
}

.overflow-hidden {
  overflow: hidden;
}

.pt-0 {
  padding-top: 0 !important;
}

.pb-0 {
  padding-bottom: 0 !important;
}

.text-center {
  text-align: center;
}
