.header {
  $this: &;
  position: fixed;
  z-index: 10;
  left: 0;
  right: 0;
  top: 0;
  &__top {
    background-color: $white;
    @include lg {
      display: none; } }

  &__bottom {
    background-color: $gray-lightest; }
  &__inner {
    @include flex(flex-start, center);
    padding-top: rem(8);
    padding-bottom: rem(8);
    #{$this}__top & {
      height: $header-top-height; }
    #{$this}__bottom & {
      height: $header-bottom-height;
      @include lg {
        height: $header-bottom-height-lg; } } }
  &__nav {
    flex-grow: 1; }
  &__nav-wrap {
    @include lg {
      position: absolute;
      z-index: 10;
      background-color: $white;
      top: 100%;
      max-height: calc(100vh - #{$header-height-lg});
      overflow: auto;
      right: 0;
      left: 0;
      @include hidden;
      @include before {
        @include arr(1em, .5em, currentColor, t);
        position: absolute;
        bottom: 100%;
        right: rem(25); } } }
  &__contacts {
    @include flex(flex-start, center);
    flex-grow: 1;
    @include lg {
      display: block;
      margin-bottom: rem(25); } }
  &__contact {
    @include notlast {
      margin-left: rem(100);
      @include xxl {
        margin-left: rem(50); }
      @include lg {
        margin-left: 0;
        margin-bottom: rem(25); } } }
  &__social {
    @include flex(flex-start, center);
    margin-left: rem(15);
    color: $gray;
    @include lg {
      margin-left: 0; } }
  &__logos-mob-wrap {
    @include flex(flex-start, center); }
  &__logos {
    font-size: 0;
    @include lg {
      margin-left: rem(15); }
    img {
      display: inline-block;
      margin-left: rem(7);
      margin-right: rem(7); } }
  &__logo {
    margin-right: rem(25);
    @include lg {
      width: 125px;
      margin-right: rem(15); } }
  &__burger {
    flex-grow: 1;
    @include lgmin {
      display: none; } }
  &__img {
    @include lg {
      width: 42px; } }
  &__meta-mob {
    padding: rem(15);
    @include lgmin {
      display: none; } } }


.header-contact {
  @include flex(flex-start, center);
  position: relative;
  @include lg {
    display: block;
    padding-right: rem(37); }
  &__text {
    @include notlast {
      margin-left: em(10); }
    &--front {
      @include lg {
        display: none; } } }
  &__link {
    font-weight: 400; }
  &__icon {
    background-color: $accent;
    width: rem(27);
    height: rem(27);
    margin-left: rem(10);
    flex: 0 0 rem(27);
    border-radius: 50%;
    font-size: 0;
    @include flex(center, center);
    .icon {
      font-size: 1rem; }
    @include lg {
      position: absolute;
      right: 0;
      top: -.15rem; } } }

.burger {
  $this: &;
  display: flex;
  align-items: center;
  justify-content: center;
  width: rem(37);
  height: rem(20);
  position: relative;
  span {
    display: block;
    &, &::after, &::before {
      width: 100%;
      height: rem(4);
      background-color: currentColor;
      backface-visibility: hidden;
      @include tr(transform .3s); }
    &::after, &::before {
      content: "";
      position: absolute;
      left: 0; }
    &::after {
      top: 0; }
    &::before {
      bottom: 0; } } }
